@import url(https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,300;0,400;0,600;1,300&display=swap);
/* import google font "Kanit" */


/* CSS reset */
* {
  font-family: 'Kanit', sans-serif!important;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}


html {
  background-color: #CC0F35!important;
}

/*
  if height value is not set on html and body,
  it will assume the same value given to children
  that is explicitly assigned a value (i.e. div#root)
*/
div#root {
  min-height: 100vh;
  /* min-height: -webkit-fill-available; */
  background-color: #CC0F35;
}

.spin-button {
  display: inline-flex;
  background-color: #F03A5F;
  color: #FFFFFF;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  padding: 0 0.4em;
  border-bottom: 1px solid hsla(10, 10%, 50%, 0.4);
  border-radius: 0 3px 0 0;
  height: 20px;
  font-size: 15px;
}

.spin-button:last-child {
  border-top: 1px solid hsla(10, 10%, 50%, 0.4);
  border-bottom: 0;
  border-radius: 0 0 3px;
}

.spin-button:active {
  transform: scale(1.1);
  box-shadow: 0 0 .5rem gold;
}

.spin-button:focus:not(:active) {
  box-shadow: 0 0 0 .125em rgba(241,70,104,.25)!important;
}

.spin-button:focus-visible {
  outline: none;
}

.add-to-cart {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 2vw;
  gap: 2vw;
  min-height: 10vh;
  min-width: 100vw;
  position: fixed;
  left: 0;
  bottom: 0;
  transition: bottom 200ms ease-in;
  border: 1px solid hsl(348, 86%, 43%);
  box-shadow: 0 0 .5rem hsl(348, 86%, 43%);
  animation: slide-in 350ms ease-out;
}

.slide-out {
  bottom: -10vh;
}

@keyframes slide-in {
  from {
    bottom: -10vh;
  }
  to {
    bottom: 0;
  }
}

/* remove arrows from number input in Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* remove arrows from number input in Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
/*  sets sidebar to overlay page main content,
    requires <main> or parent position: relative (!== static) */
.nav-menu {
  position: absolute;
  /* position: fixed; */
  background-color: #ab1236;
  z-index: 1;
  transition: left 250ms ease-in;
  padding: 0px .5rem;
  /* top: +24px; */ /* burger icon height */
  top: calc(48px + 30px); /* parent padding + burger icon height */
  /* bottom: 0; */ /* -height of footer to reach bottom of document */
  bottom: -68px; /* -height of footer to reach bottom of document */
  /* left: calc(-130px + -24px); */ /* offset main element padding + menu width */
  left: -132px; /* width of nav-meny */
  border-radius: 0px 4px 4px 0px;
  border-color: gold;
  box-shadow: 0 0 .4rem gold;
}

/* slides sidebar to view when active */
.nav-menu.is-active {
  /* left: -24px; */ /* offset main element padding */
  left: 0;
  transition: left 450ms ease-out;
}

/* highlights active nav item */
li.is-active {
  background-color: #ffffff;
  border-radius: 5px;
}

/* add gap between nav links in sidebar and icreases size of container */
.sidebar-links li {
  margin-top: .5rem;
  padding: .5rem 1rem;
}

/* set nav links font color except for link */
.sidebar-links li a:not(.active-font) {
  color: #ffe08a!important;
}

button {
  background-color: transparent;
  border: none;
}
* {
  box-sizing: border-box;
}

/* prevents embedded map from overflowing container */
iframe {
  width: calc(100%);
}

/* hover effect for menu items */
div.card:hover,
.hovered {
  transform: scale(1.05);
  border-color: gold;
  box-shadow: 0 0 .5rem gold;
}

div.card.no-scale:hover {
  transform: none;
}

div.card.no-scale {
  border-color: gold;
  box-shadow: 0 0 .5rem gold;
}

div.message-body {
  border-width: 3px!important;
}

/* for Link components */
.active-font {
  color: #C79200!important;
}

.is-transparent {
  background-color: transparent!important;
  box-shadow: none!important;
}

.is-justify-right-desktop {
  justify-content: space-between;
}

.has-text-bgc {
  color: hsl(348, 86%, 43%);
}

/* cart content count badge for <i> element of cart button*/
.badge:after {
  content: attr(value);
  font-family: 'Kanit', sans-serif!important;
  color: hsl(348, 86%, 43%);
  min-width: 19px;
  font-size: .8rem;
  background: #ffe08a;
  border-radius: 50%;
  padding: 0 5px;
  position: relative;
  left: -8px;
  top: -10px;
  opacity: 0.9;
}

.donate-container {
  grid-gap: .5rem;
  gap: .5rem;
}

.copy-notification {
  display: none!important;
  opacity: 0%;
  position: absolute;
  left: 0;
  right: 0;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  animation: fade-in 2000ms ease-out forwards;
  opacity: 0;
  transform: translateY(25%);
}

.copy-notification.is-active {
  display: flex!important;
}

@keyframes fade-in {
  20% {
    opacity: 1;
    transform: translateY(0);
  }
  80% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(25%);
  }
}

.modal-background {
  background-color: rgba(10,10,10,.70)!important;
}

.modal {
 animation: fade 200ms ease-in-out forwards;
 opacity: 0;
}

@keyframes fade {
  100% {
    opacity: 1;
  }
}

/* if display is less than or equal to 1023px (less than desktop) */
@media (max-width: 1023px) {
  /* for sidebar position: absolute to achieve height: 100% */
  main {
    position: relative;
    height: 100%;
    /* fills remaining height of parent; requires parent display: flex */
    flex: 1 1;
  }

  article {
    width: 100%;
    max-width: 512px;
  }

  /* allows <main> to fill remaining space of its parent container */
  div#root {
    display: flex;
    flex-direction: column;
  }

  /* allows contact details and map to wrap on touch devices */
  div.message-body.is-flex {
    flex-wrap: wrap;
  }
  
  .img-container {
    width: 100%;
    height: 100%;
  }
}

/* display is greater than or equal to 1024px (desktop or greater only) */
@media (min-width: 1024px) {
    article {
      /* width: 50%; */
      width: 100%;
  }
  
  .img-container {
    width: 66%;
    height: 66%;
  }

  .is-justify-right-desktop {
    justify-content: flex-end;
  }
}

@media (max-width: 399px) {
  .hero-body {
    padding: 1rem 0 0!important;
  }
}

/*
  keeps footer at the bottom of page
  requires div#root min-height 100vh
*/
#root > footer {
  position: -webkit-sticky;
  position: sticky;
  top: 100vh;
  background-color: transparent;
  z-index: 2;
}

